<template>
  <div class="pageContent">
    <el-card shadow="always">
      <div class="head">请选择您要配置的收款场景</div>
      <div class="flex-row-start">
        <el-card shadow="hover" class="box mr40">
          <div class="activeBox">
            <div class="cardBox flex-column-center" @click="mpConfig">
              <div>
                <el-image style="width: 100px; height: 100px" :src="require('../../../assets/images/wechat/wechat.png')" fit="fill"></el-image>
              </div>
              <div class="boxHead">微信公众号</div>
              <div class="boxFont">消费者在商户的公众号内进行付款</div>
            </div>
          </div>
        </el-card>
        <el-card shadow="hover" class="box flex-column-center">
          <div class="activeBox" @click="miniConfig">
            <div class="cardBox flex-column-center">
              <div>
                <el-image style="width: 100px; height: 100px" :src="require('../../../assets/images/wechat/mp.png')" fit="fill"></el-image>
              </div>
              <div class="boxHead">微信小程序</div>
              <div class="boxFont">消费者在商户的小程序内进行付款</div>
            </div>
          </div>
        </el-card>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopPayType } from '@/api/moneyConfig/payConfig'
export default {
  data(){
    return{

    }
  },
  created(){
    this.getData()
  },
  methods:{
    getData(){
      getShopPayType(this.shop.shopId).then(res=>{
        console.log('店铺配置',res)
        this.payType = res.data.payType + ''
        this.mpPayStatus = res.data.mpPayStatus + ''
        this.miniPayStatus = res.data.miniPayStatus + ''
        this.mpAuthStatus = res.data.mpAuthStatus
        this.miniAuthStatus = res.data.miniAuthStatus
      })
    },
    mpConfig(){
      if(this.mpAuthStatus == 0){
        this.$confirm('您尚未授权公众号', '提示', {
          confirmButtonText: '去授权',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          console.log('点了去授权')
          this.$router.push({path:'/weChat/mpManage'})
        }).catch(() => {});
        return;
      }
      this.$router.push({
        path:'/moneyConfig/payConfig/configList',
        query:{
          payType:1
        }
      })
    },
    miniConfig(){
      if(this.miniAuthStatus == 0){
        console.log('错了')
        this.$confirm('您尚未授权小程序', '提示', {
          confirmButtonText: '去授权',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          console.log('点了去授权')
          this.$router.push({path:'/weChat/miniManage'})
        }).catch(() => {});
        return;
      }
      this.$router.push({
        path:'/moneyConfig/payConfig/configList',
        query:{
          payType:2
        }
      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .head{
    width: 100%;
    line-height: 80px;
    font-size: 18px;
    font-weight: bold;
  }
  .box{
    cursor: pointer;
    width: 400px;
    height: 400px;
    &.mr40{
      margin-right: 40px;
    }
    /deep/ .el-card__body{
      width: 100%;
      height: 100%;
      .cardBox{
        margin: auto;
      }
      .activeBox{
        width: 100%;
        height: 100%;
        display: flex;
      }
    }
    .boxHead{
      font-size: 16px;
      font-weight: bold;
      color: #303133;
    }
    .boxFont{
      margin-top: 10px;
      font-size: 14px;
      color: #909399;
    }
  }
</style>